import { Component, OnInit, Input, AfterViewInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Observable, of, range, timer, throwError } from 'rxjs';
import { catchError, switchMap, shareReplay, tap, skipWhile, debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { BookingService } from 'src/app/shared/services/booking/booking.service';
import { CreateComponent } from '../create/create.component';
import { DeviceService } from 'src/app/shared/services/client/device.service';
import { UserService } from 'src/app/shared/services/user/user.service';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Message } from 'src/app/shared/models/Message';

declare const $: any;
@Component({
  selector: 'app-records',
  templateUrl: './records.component.html',
  styleUrls: ['./records.component.scss']
})
export class RecordsComponent implements OnInit, AfterViewInit {
  @Input('recipient') recipient: any = this.userService.getuser();
  @Input('me') me: any = this.userService.getuser();
  messages: any[] = [];
  ngAfterViewInit(): void {
  }

  dialog: any = null;
  searching: boolean = false;
  foundResult: boolean = false;
  list: any[] = [];
  user: any = this.userService.getuser();
  chatList: any[] = [
    {
      code: 'ZT-U1',
      id: '1',
      email: 'test@info.com',
      firstName: 'Zinder',
      lastName: 'Admin',
      passport: null,
      status: 'Active',
      lastSeen: 'Now'
    }
  ];
  _chatList: any[] = [
    {
      code: 'ZT-U1',
      id: '1',
      email: 'test@info.com',
      firstName: 'Zinder',
      lastName: 'Admin',
      passport: null,
      status: 'Active',
      lastSeen: 'Now'
    }
  ];
  searchContDown: any = 1;
  searchContDown$: Observable<any> = timer(1000).pipe(tap(c => {
    if (c > 98) {
      this.searching = false;
    }
  })
  );
  searchKeyForm: FormGroup = this.formBuilder.group({
    query: new FormControl('', [Validators.required, Validators.email])
  });
  event$: Observable<any> = this.deviceService.getEventsApi({
    who_like: this.user.code
  });
  selectedChat: any = null;
  // chats:Observable<any>=
  contact$: Observable<any> = this.deviceService.getContacts({ who_like: this.me.code}).pipe(
    shareReplay(1)
  )
  // contacts$: Observable<any> = this.contacts.pipe(
  //   switchMap((r:any)=>{
  //     if(r.length < 1){
  //       return this.deviceService.newContact()
  //     }else{

  //     }
  //   })
  // );
  constructor(private _bs: BookingService,
    private userService: UserService,
    private formBuilder: FormBuilder,
    private deviceService: DeviceService,
    public dialogModule: MatDialog) { }

  ngOnInit(): void {33
    this.event$.subscribe(c => this.list = c);
    this.searchKeyForm.controls.query.valueChanges.pipe(
      skipWhile(v => v.length < 2),
      debounceTime(1500),
      distinctUntilChanged()
    ).subscribe(
      value => {

        this.searchUser(value).subscribe(

        )
      }
    );
    let existinglistv = this.deviceService._conversations.value.get(this.recipient.code);
    if (existinglistv) {
      this.messages = existinglistv;
    }
    this.contact$.subscribe();
  }
  uploadedEventHandler(event: any) {
    event.sender = this.me.code;
    event.recipient = this.recipient.code;
    event.mail = null;
    let mail = {
      from: this.me.email, //replace with your email
      to: this.recipient.email,
      subject: `New Message from ${this.me.firstName} via zinder`,
      html: null,
      innerBody: event.content,
    }
    if (this.isHtml(event.content)) {
      mail.html = event.content;
    }
    this.deviceService.mail(mail)
      .pipe(
        catchError((e: any) => {
          return throwError(e);
        })
      )
      .subscribe(

      )

    this.messages.push(event);

  }
  sendEventHandler(event: any) {
    event.sender = this.me.code;
    event.recipient = this.recipient.code;
    event.status = 'Sent';
    event.mail = null;
    let mail = {
      from: this.me.email, //replace with your email
      to: this.recipient.email,
      subject: `New Message from ${this.me.firstName} via zinder messanger`,
      html: null,
      innerBody: event.content,
    }
    if (this.isHtml(event.content)) {
      mail.html = event.content;
    }
    this.deviceService.sendMessage(event).pipe(
      switchMap((res: any) => {

        if (res.id) {
          this.updateFeed(res);
          return this.deviceService.mail(mail)
            .pipe(
              switchMap((_res: any) => {

                if (_res.created_at) {
                  res.mail = _res;
                  res.status = 'Delivered';
                  this.updateFeed(res);
                  res.status = 'Unseen';
                  return this.deviceService.updateMessage(res)
                    .pipe(
                      catchError((e: any) => {

                        return throwError(
                          new Error('Update Error')
                        );
                      })
                    )
                }
                return of(res);
              }),
              catchError((e: any) => {

                return throwError(
                  new Error("Mail Error")
                );
              })
            )
        }
        return of(res);
      }),
      catchError((e: any) => {

        return throwError(
          new Error("Sending Message Error")
        );
      })
    )
      .subscribe(
        c => {
          this.updateFeed(c);
          this.gotoBottom();
        },
        e => console.error(e)
      )
  }

  updateFeed(message: Message) {
    let existinglistv = this.deviceService._conversations.value;
    let existinglist = this.threads;
    const loadedIndex = existinglist.findIndex(m => m.code == message.code);
    if (loadedIndex > -1) {
      existinglist[loadedIndex] = message;
    } else {
      existinglist.push(message);
    }
    existinglistv.set(this.recipient.code, existinglist);
    this.deviceService._conversations.next(existinglistv)
  }

  set selectChat(chat: any) {
    this.selectedChat = chat;
    setTimeout(() => {
      this.gotoBottom();
    }, 900);
  }

  gotoBottom() {
    let element: any = document.getElementById(`chatmessaggeboxid`);
    if(element){
      element.scrollIntoView({ top: element.scrollHeight, behavior: 'smooth' });
    }
  }
  searchUser(value: any) {
    let sort = {
      email_like: value
    }
    this.searching = true;
    this.foundResult = false;
    this.searchContDown$.subscribe();
    return this.userService.getSortedUsersApi(sort).pipe(
      tap(r => {
        this.searching = false;
        this.foundResult = true;
        this.list = r;
      })
    );
  }
  newEvent() {
    return this.dialogModule.open(CreateComponent, {
      hasBackdrop: true,
      disableClose: true,
      data: {
        spinnerName: `Create-Booking-Spinner`,
        actionDone: async (e: any) => {  },
      },
    })
  }
  getReadableTime(time: any) {
    const _time = new Date(time);
    return this.getFormattedDate(time);
    // return _time.toUTCString();
  }
  getFormattedDate(_date: any) {
    let date = new Date(_date);

    let month: any = date.getMonth() + 1;
    let day: any = date.getDate();
    let hour: any = date.getHours();
    let min: any = date.getMinutes();
    let sec: any = date.getSeconds();

    month = (month < 10 ? "0" : "") + month;
    day = (day < 10 ? "0" : "") + day;
    hour = (hour < 10 ? "0" : "") + hour;
    min = (min < 10 ? "0" : "") + min;
    sec = (sec < 10 ? "0" : "") + sec;

    var str = hour + ":" + min + ":" + sec + ", " + date.getFullYear() + "-" + month + "-" + day;

    /*alert(str);*/

    return str;
  }
  get threads() {
    return this.deviceService._conversations.value.get(this.recipient.code) || [];
  }
  getTime(time: any) {
    return new Date(time).toLocaleDateString()
  }

  isHtml(content: string) {
    let regexForHTML = /<([A-Za-z][A-Za-z0-9]*)\b[^>]*>(.*?)<\/\1>/;
    return regexForHTML.test(content);
  }

}
