import { Component, OnInit, Inject, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AngularEditorConfig } from '@kolkov/angular-editor';
import { Observable , of, throwError} from 'rxjs';
import { distinctUntilChanged, catchError, skipWhile,map, switchMap, debounceTime, tap, shareReplay } from 'rxjs/operators';
import { DeviceService } from 'src/app/shared/services/client/device.service';
import { FileService } from 'src/app/shared/services/client/file.service';
import { CompanyService } from 'src/app/shared/services/company/company.service';
import { UserService } from 'src/app/shared/services/user/user.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss']
})
export class CreateComponent implements OnInit {
  user:any = this._uservice.getuser();
  reoccuring:boolean = false;
  seaecheKey:string = '';
  typeofSelected:string = '';
  starts:string = '';
  errMessage:string = '';
  successMessage:string = '';
  ends:string = '';
  title:string = '';
  today: any = new Date(Date.now());
  searchResult:EventEmitter<any> = new EventEmitter();
  addedUser: any[] = [this.user];
  searchResult$:Observable<any> = this.searchResult.pipe(
    distinctUntilChanged(),
      skipWhile(c=>c.length< 3),
      debounceTime(900),
      switchMap(r=>{
        if(this.seaecheKey.length < 2){
          return of([]);
        }
        return this._uservice.getSortedUsersApi({code_ne: this.user.code, firstName_like: this.seaecheKey, email_like: this.seaecheKey}).pipe(
          //
          // shareReplay(1)
          map(__r=>__r.filter(_r=>!this.addedUser.find(_r$=>_r$.code == _r.code)))
        )
      }),

      shareReplay()
  )
  editorConfig: AngularEditorConfig = {
    editable: true,
    spellcheck: true,
    height: 'auto',
    minHeight: '50',
    maxHeight: 'auto',
    width: 'auto',
    minWidth: '0',
    translate: 'yes',
    enableToolbar: true,
    showToolbar: true,
    placeholder: 'Enter text here...',
    defaultParagraphSeparator: '',
    defaultFontName: '',
    defaultFontSize: '',
    fonts: [
      { class: 'arial', name: 'Arial' },
      { class: 'times-new-roman', name: 'Times New Roman' },
      { class: 'calibri', name: 'Calibri' },
      { class: 'comic-sans-ms', name: 'Comic Sans MS' }
    ],
    customClasses: [
      {
        name: 'quote',
        class: 'quote',
      },
      {
        name: 'redText',
        class: 'redText'
      },
      {
        name: 'titleText',
        class: 'titleText',
        tag: 'h1',
      },
    ],
    uploadUrl: `${environment.uploadApi}/upload`,
    upload: (file: File) => {

      let formData = new FormData();
      formData.append('file', file, file.name)
      return this._fs.upload(formData)
        .pipe(
          catchError((err: any) => {

            return throwError(err);
          }),
          tap((res:any)=>{


          })
        );
      },
    uploadWithCredentials: false,
    sanitize: true,
    toolbarPosition: 'top',
    toolbarHiddenButtons: [
      ['bold', 'italic'],
      ['fontSize']
    ]
  };
  message:any = '';
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
  private _uservice: UserService,
  private _fs: FileService,
  private _cs: CompanyService,
  private deviceService: DeviceService) {
    this.today.setDate(this.today.getDate());
    // this.today.setDate(this.today.getDate() - 1);
    this.today = this.today.toISOString().slice(0, 16);
  }

  ngOnInit(): void {

  }
  saveNew($event:any){
    let message = '';
    this.errMessage = '';
    this.successMessage = '';
    if(!this.typeofSelected){
      message+='Is this a meeting/task event.\n';
    }
    if(!this.starts){
      message+='Time to start is required.\n';
    }
    if(!this.ends){
      message+='Time to end is required.\n';
    }
    if(!this.title){
      message+='Title is required\n';
    }
    this.errMessage = message;
    if(message.length >1){
      this.deviceService.oErrorNotification('Opps', message);
      return;
    }
    let newObj = {
      type: this.typeofSelected,
      start: this.starts,
      end: this.ends,
      // code:`Z-${this.typeofSelected}-${Date.now()}`,
      title: this.title,
      message: this.message,
      company: this._cs.runningCompany.code,
      creator: this.user.code,
      code: 'Z-'+this.typeofSelected+Date.now(),
      notifified: false,
      status: 'upcoming',
      created_at: new Date(Date.now()),
      updated_at: new Date(Date.now()),
      reoccuring: this.reoccuring,
      users: this.addedUser.map(
        u=>{return {email: u.email,
                    code: u.code,
                    membership: (u.code == this.user.code) ? 'host': 'invite'}}
      )
    }
    this.successMessage =  `${newObj.title} successfully saved. ${newObj.users.length} Participants`;
    this.deviceService.newEventsApi(newObj).subscribe(
      data=>{

        this.deviceService.oSuccessNotification(`${newObj.type} Created`, this.successMessage);
        this.data?.actionDone($event);
      }
    )

  }
  nameSearchChange($evet){

    this.searchResult.emit($evet)
    this.searchResult.subscribe()
    this.searchResult$.subscribe()
  }
  addUser(user:any){
    this.addedUser.push(user);
    this.seaecheKey = '';
    this.nameSearchChange(this.seaecheKey);
  }
  addedUserAlready(code:string){
    return this.addedUser.indexOf(u=>u.code == code) > -1;
  }
  remove(code:any){
    this.addedUser = this.addedUser.filter(u=>u.code != code);
  }

}
